const app = getApp();
export default {
	data() {
		return {
			search_word: '',
			hotKw: [],
			hotArea: false,
			hisKw: [],
			hisArea: false,
			mhKw: [],
			focus: true,
			on_input: false,
			res: false,
			params: {
				year: true,
				month: false,
				day: false
			},
			showGrade: false,
			showMajor: false,
			showRegion: false,
			showPreRegion: false,
			curGrade: '',
			curYear: '',
			curMajor: '专业',
			majorList: [],
			hyList: [],
			curRegion: '',
			chooseRegin: '',
			curPreRegion: '',
			choosePreRegion: '',
			defaultRegion: ['浙江省', '杭州市', '西湖区'],
			showMajorHy: false,
			showTags: false,
			default_avatar: '../../static/default-avatar.png',
			alumni_avatar: '../../static/alumni_avatar.jpg',
			alumni_sex: 2,
			male_icon: '../../static/male.png',
			female_icon: '../../static/female.png',
			is_collect: 1,
			hoverClass: 'hover2',
			itemStyle: {
				color: '#595E75',
				fontSize: '28rpx',
				marginBottom: '18rpx'
			},
			headStyle: {
				color: '#595E75'
			},
			alumniList: [],
			collected: true,
			res_num: 0,
			f_title: '',
			f_label: [],
			s_title: '',
			s_label: [],
			choose_mojar: '',
			choose_hy: '',
			majorIndex: -1,
			hyIndex: -1,
			rSelect: [],
			selectList: [],
			selectId: [],
			searching: false,
			page: 1,
			is_all:false,
			listlength:0,
			dataList: [],
			clickMore: false
		};
	},
	watch: {
		'search_word': {
			handler(value) {
				if(this.search_word != '') {
					if (this.timer) {
						clearTimeout(this.timer)
					}
					this.timer = setTimeout(() => {
						// console.log('aaa');
						this.getAlumniList();
					}, 1000)
				}
				// if (this.search_word == '') {
				// 	this.getAlumniList()
				// }
			},
			deep: true
		}
	},
	onLoad() {
		if(app.reToken()) {
			uni.getStorageSync("access_token")
		}
		this.getHotKw();
		this.getHisKw();
		this.getMajorList();
		this.getHyList();
		this.getLabelList();
		var date=new Date;
		this.curYear = date.getFullYear()
	},
	methods: {
		sList() {
			for(var i = 0; i < this.rSelect.length; i ++) {
				this.selectId.push(this.rSelect[i].id);
				this.selectList.push(this.rSelect[i].title);
			}
			// this.getAlumniList()
		},
		getMore() {
			this.page += 1;
			this.clickMore = true;
			this.getAlumniList()
		},
		getAlumniList() {
			uni.request({
				url: getApp().globalData.url + 'api/index/index/search',
				method: 'POST',
				data: {
					keywords: this.search_word,
					nj: this.curGrade,
					zy: this.choose_mojar,
					hy: this.choose_hy,
					area: this.chooseRegin,
					syd: this.choosePreRegion,
					tags: this.selectId,
					page: this.page
				},
				header: {
					Authorization: 'Bearer ' + uni.getStorageSync('access_token')
				},
				success: (res) => {
					// console.log(res)
					if(this.clickMore) {
						this.alumniList = this.alumniList.concat(res.data.items.data)
						this.clickMore = false
					}else {
						this.alumniList = res.data.items.data;
					}
					// console.log(this.alumniList)
					this.res_num = this.alumniList.length
					this.searching = true
					if(this.alumniList.length > 0) {
						this.res = true
						// this.alumni_none = false
					}
					if(this.listlength == this.alumniList.length || this.alumniList.length < 15) {
						this.is_all = true
					}
					if(this.alumniList.length - this.listlength < 15) {
						this.is_all = true
					}
					this.listlength = this.alumniList.length;
					// this.page += 1
				},
				fail: (res) => {
					
				}
			})
		},
		closeCur() {
			uni.switchTab({
				url: './index'
			})
		},
		onKeyInput(e) {
			if(e!='') {
				// console.log(e)
				this.on_input = true;
				this.getAlumniList();
				this.getMhKw();
			}else {
				this.on_input = false
			}
		},
		//选择专业
		c_mojar(e, index) {
			this.choose_mojar = e.zy
			this.majorIndex = index
		},
		c_hy(e, index) {
			this.choose_hy = e.title
			this.hyIndex = index
		},
		//选择标签
		labelStatus(value) {
			if(this.rSelect.indexOf( value )!=-1){  
				this.rSelect.splice( this.rSelect.indexOf(value), 1 );  
			}else{  
				if(this.rSelect.length <= 4) {
					this.rSelect.push(value);  
				}else {
					uni.showToast({
						icon: 'none',
						title: '最多可选5个标签',
					});
				}
			}
			// console.log(this.rSelect)
		},
		confirmGrade(e) {
			this.curGrade = ''
			this.curGrade = e.year;
			// this.search_word = this.curGrade
			this.getAlumniList();
		},
		// confirmMajor(e) {
		// 	this.curMajor = '';
		// 	e.map((val, index) => {
		// 		this.curMajor = val.label
		// 	})
		// 	this.getAlumniList();
		// },
		confirmRegion(e) {
			// this.curRegion = ''
			this.chooseRegin = e.province.label + ' ' + e.city.label + ' ' + e.area.label;
			this.curRegion = e.area.label;
			// this.search_word = this.curRegion
			this.getAlumniList();
		},
		confirmPreRegion(e) {
			this.curPreRegion = ''
			this.curPreRegion = e.area.label;
			this.choosePreRegion = e.province.label + ' ' + e.city.label + ' ' + e.area.label;
			// this.search_word = this.curPreRegion
			this.getAlumniList();
		},
		// moreSele() {
		// 	this.more_sele = !this.more_sele
		// },
		major_hy() {
			// console.log(111)
			this.showMajorHy = !this.showMajorHy
			this.res = false;
			if(this.showTags == true) {
				this.showTags = false
			}
		},
		showTag() {
			this.showTags = !this.showTags
			this.res = false;
			if(this.showMajorHy == true) {
				this.showMajorHy = false
			}
		},
		resetMajorBtn() {
			this.choose_mojar = ''
			this.choose_hy = '';
			this.majorIndex = -1;
			this.hyIndex = -1;
		},
		confirmMajorBtn() {
			this.showMajorHy = false
			// this.search_word = this.choose_mojar + '，' + this.choose_hy
			this.getAlumniList()
		},
		resetBtn() {
			// this.showTags = false
			this.rSelect = []
		},
		confirmBtn() {
			this.showTags = false;
			this.sList();
			// this.search_word = this.selectList
			this.getAlumniList()
		},
		toResPage() {
			this.on_input = false
		},
		addCol(id, i) {
			var _this = this;
			var requestCode = uni.request({
				url: getApp().globalData.url + 'api/user/collect',
				method: 'POST',
				data: {
					info_id: id
				},
				header: {
					Authorization: 'Bearer ' + uni.getStorageSync('access_token')
				},
				success: (res) => {
					if (res.data.errorCode == 0) {
						_this.alumniList[i].collected = !_this.alumniList[i].collected;
						if(_this.alumniList[i].collected == true) {
							uni.showToast({
								icon: 'none',
								title: '收藏成功',
							});
						}else {
							uni.showToast({
								icon: 'none',
								title: '取消收藏成功',
							});
						}
						uni.setStorageSync("reload", true);
					}
				},
				fail: (res) => {
					console.log(res);
					uni.showToast({
						icon: 'none',
						title: '请求失败，请稍后再试',
					});
				},
			})
		},
		//获取专业
		getMajorList() {
			uni.request({
				url: getApp().globalData.url + 'api/common/zy',
				method: 'get',
				header: {
					Authorization: 'Bearer ' + uni.getStorageSync('access_token')
				},
				success: (res) => {
					// console.log(res)
					this.majorList = res.data.items
				},
				fail: (res) => {
					
				}
			})
		},
		//获取行业
		getHyList() {
			uni.request({
				url: getApp().globalData.url + 'api/common/hy',
				method: 'get',
				header: {
					Authorization: 'Bearer ' + uni.getStorageSync('access_token')
				},
				success: (res) => {
					this.hyList = res.data.items
				},
				fail: (res) => {
					
				}
			})
		},
		//获取标签
		getLabelList() {
			uni.request({
				url: getApp().globalData.url + 'api/common/tags',
				method: 'get',
				header: {
					Authorization: 'Bearer ' + uni.getStorageSync('access_token')
				},
				success: (res) => {
					// console.log(res)
					// this.tagsList = res.data.items;
					// this.f_title = res.data.items[0].title
					// this.f_label = res.data.items[0].children
					// this.s_title = res.data.items[1].title
					// this.s_label = res.data.items[1].children
					var data = res.data.items
					for(var i = 0; i < data.length; i ++) {
						this.dataList.push({
							title: data[i].title,
							label: data[i].children
						})
					}
				},
				fail: (res) => {
					
				}
			})
		},
		getHotKw() {
			uni.request({
				url: getApp().globalData.url + 'api/index/index/history',
				method: 'get',
				header: {
					Authorization: 'Bearer ' + uni.getStorageSync('access_token')
				},
				success: (res) => {
					// console.log(res)
					this.hotKw = res.data.items
					if(this.hotKw.length > 0) {
						this.hotArea = true
					}
				},
				fail: (res) => {
					
				}
			})
		},
		getHisKw() {
			uni.request({
				url: getApp().globalData.url + 'api/user/history',
				method: 'get',
				header: {
					Authorization: 'Bearer ' + uni.getStorageSync('access_token')
				},
				success: (res) => {
					// console.log(res)
					this.hisKw = res.data.items
					if(this.hisKw.length > 0) {
						this.hisArea = true
					}
				},
				fail: (res) => {
					
				}
			})
		},
		//获取模糊搜索词
		getMhKw() {
			uni.request({
				url: getApp().globalData.url + 'api/index/index/keywords',
				method: 'get',
				header: {
					Authorization: 'Bearer ' + uni.getStorageSync('access_token')
				},
				data: {
					keywords: this.search_word
				},
				success: (res) => {
					// console.log(res)
					this.mhKw = res.data.items;
				},
				fail: (res) => {
					
				}
			})
		},
		mhWord(item) {
			this.search_word = item.name
			this.on_input = false
		},
		addKeyword(e) {
			this.search_word = e.keywords
		},
		//清空历史记录
		hisDel() {
			uni.request({
				url: getApp().globalData.url + 'api/user/history',
				method: 'DELETE',
				header: {
					Authorization: 'Bearer ' + uni.getStorageSync('access_token')
				},
				success: (res) => {
					this.hisArea = false
					uni.showToast({
						icon: 'none',
						title: '历史记录已全部清空',
					});
				},
				fail: (res) => {
					
				}
			})
		}
	}
}
